
<div class="simple-bar-chart-wrapper px-5 py-3">
  <div class="clearafter py-2">
    <div class="simple-bar-chart-label text-base font-bold">
      {{ title }}
    </div>
    <div class="simple-bar-chart-value text-base font-bold">
      {{ statFormatter(series[0]) }}
    </div>
  </div>
  <div class="py-2">
    <div class="simple-bar-chart-track rounded-full bg-gray-200">
      <div
        :style="{ width: series[0] + '%', backgroundColor: barColor }"
        class="simple-bar-line h-3 rounded-full"
      />
    </div>
  </div>
</div>
